import React from "react"
import styled from "styled-components"
import { MyLayout, MyLayoutHelpers } from "@macashipo/mlib"
import { fnListMyFormControl } from "src/helpers/fnList"
const Wrap = styled.div`
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  & .alert {
    padding: 0.5rem;
    margin-bottom: 0.5rem;
  }
  & > div:first-child {
    width: 100px;
  }
  & > div:last-child {
    flex: 1;
  }
`
export default function MFCWrapControl(props) {
  const { cfc, noLabel } = props
  const configWrapControl = props.configWrapControl || {}
  return (
    <Wrap className="mfcw-ngang" style={configWrapControl.style}>
      {noLabel !== true && (
        <div style={configWrapControl.styleLabel}>{cfc.label}</div>
      )}
      <div style={configWrapControl.styleControl}>{props.children}</div>
    </Wrap>
  )
}
